@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Overlock);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*:after,
*:before {
    box-sizing: border-box;
}

body {
    background: #cef;
}

.container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}

.widget {
    background: linear-gradient(to bottom right, #3cc0fe 20%, #0066ff);
    width: 900px;
    height: 500px;
    border-radius: 6px;
    box-shadow: 0px 60px 80px -20px rgba(39, 165, 253, 0.4);
    position: relative;
    overflow: hidden;
}

.details {
    font-family: Roboto, sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 30px;
    margin-left: 50px;
}

.temperature {
    color: white;
    font-weight: 300;
    font-size: 10em;
}

.city {
    color: #d2e9fa;
    font-size: 2.2em;
    font-weight: 300;
    width: 500px;
    padding-bottom: 16px;
    border-bottom: 2px solid #9cd0ff;
    margin-left: 8px;
}

.summary {
    display: -webkit-flex;
    display: flex;
    color: #d2e9fa;
    font-size: 2em;
    font-weight: 300;
    width: 90%;
    margin-top: -16px;
    padding-bottom: 16px;
    border-bottom: 2px solid #9cd0ff;
    margin-left: 8px;
}

.summaryText {
    width: 40%;
    margin: 0;
    margin-left: 26px;
}

.sunsetAndSunrise {
    width: 40%;
    margin: 0;
    margin-left: 26px;
}

.precipitation,
.wind {
    color: #d2e9fa;
    font-size: 1.6em;
    font-weight: 300;
    margin-left: 8px;
}

.precipitation {
    margin-top: 16px;
}

.wind {
    margin-top: 4px;
}

.error {
    margin-left: 20px;
    color: #0066ff;
    font-weight: 400;
    font-size: 2em;
}
.App input {
  border-radius: 6px;
  font-size: 2em;
  margin: 20px;
  padding: 10px 20px;
  border: 2px solid gray;
  background-color: #cef;
}

.App button {
  border-radius: 6px;
  font-size: 2em;
  margin: 20px;
  padding: 10px 20px;
  border: 2px solid gray;
  background-color: white;
  transition: .3s;
  cursor: pointer;
}

.App button:hover {
  background-color: #0066ff;
  color: #cef;
  border-color: #0066ff;
}
